import React, { FC, useContext } from 'react';
import { WebchatContext } from '@botonic/react';
import styles from './CustomButton.module.css';
// eslint-disable-next-line css-modules/no-unused-class
import commonStyles from '../../styles/common.module.css';

interface Props {
  trigger: string;
  label: string;
  submitHandle: () => void;
}

const CAN_SUBMIT = 'start_the_chat';

export const CustomButton: FC = React.forwardRef((props: Props, ref) => {
  const { trigger, label, submitHandle } = props;
  const {
    webchatState: { theme },
  } = useContext(WebchatContext);

  return (
    <div className={styles.customButton}>
      <div className={commonStyles.placeholder} />
      <div className={styles.wrapper}>
        <button
          type="button"
          className={styles.submitButton}
          ref={ref}
          data-qa="test-custom-button"
          title={label}
          style={{
            fontSize: theme?.initialScreen?.customButtonFontSize,
            fontFamily: theme?.initialScreen?.customButtonFontFamily,
            color: theme?.initialScreen?.customButtonColor,
            backgroundColor: theme?.initialScreen?.customButtonBackgroundColor,
            borderColor: theme?.initialScreen?.customButtonBorderColor,
            height: theme?.initialScreen?.customButtonHeight,
          }}
          onClick={() => {
            if (trigger === CAN_SUBMIT) {
              submitHandle();
            }
          }}
        >
          {label}
        </button>
      </div>

      <div className={commonStyles.placeholder} />
    </div>
  );
});
